@import url(https://fonts.googleapis.com/css?family=Montserrat|Work+Sans&display=swap);
*{margin:0;padding:0;box-sizing:border-box;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}body{font-family:Montserrat;overflow:hidden}#welcome{height:100vh;width:90vw}#welcome .container{position:absolute;top:35%;left:12.5%;display:grid;grid-template-columns:1fr;grid-row-gap:3rem}#welcome .container h3{font-weight:200;color:#D8B269}#welcome .container #description{display:grid;grid-row-gap:.5rem;align-items:center;justify-items:flex-start}#welcome .container #description h4{font-weight:200;color:#B8BCBE}#welcome .container #description h1{font-weight:600;font-size:3rem}#welcome .container #description h1 span{border:3px solid #D8B269;box-sizing:content-box;padding:3px 30px 0px 1px}#welcome .container #description h3{font-family:"Work Sans", Montserrat}

